.pagination__wrapper{
  display: flex;
  justify-content: center;
  .pagination{
    margin: 20px 0;
    display: inline-block;
    padding-left: 15px;
    padding-right: 15px;
    & > * {
      &:not(:last-child){
        margin-right: 3px;
      }
    }
  }
  .selected{
    a{
      color: black;
    }
    background: white;
  }
  li{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    transition: all linear .3s;
    font-size: 20px;
    border-radius: 10px;
    &:hover{
      background: white;
      .arrow__icon{
        fill:  black;
      }
    }
  }
  .disabled{
    display: none;
  }
  .arrow-prev{
    transform: revert;
  }
  .arrow-icon{
    transition: all linear .3s;
    fill:  #3b4954;
    margin-bottom: 3px;
    width: 15px;
    height: 15px;
  }
  a{
    padding: 5px 10px;
    color: #5c6c79;
  }
}