.block-trending{
  padding: 30px 40px;
  &__categories{
    & > * {
    	&:not(:last-child){
    		margin-right: 10px ;
    	}
    }
  }
}
.trending-container{
  flex-wrap: wrap;
  display: flex;
  flex: 1 ;
  justify-content: flex-start;
}
.categories{
  &-block{
    display: flex;
    & > * {
    	&:not(:last-child){
    		margin-right: 40px ;
    	}
    }
  }
}