.genres-block{
  background: #F0F0F0;
  padding: 40px;
  text-transform: uppercase;
  position: relative;
  h1{
    margin-bottom: 30px;
  }
  .slick-dots{
    right: 50%;
    transform: translateX(50%);
    width: auto;
    height: 30px;
    top: -35px;
    display: inline-block !important;
    position: absolute;
    
  }
}
.arrow-prev{
  top: -28px !important;
  transform: rotate(180deg);
  right: 54.6%;
}
.arrow-next{
  right: 44%;
}

