.discover-form{
  display: flex;
  margin-bottom: 40px;
  & > * {
    width: 25%;
    &:not(:last-child){
      margin-right: 30px ;
    }
  }
  &__element{
    &:last-child{

      flex-grow: 1;
      .input-range{
        width: 95%;
        margin-top: 10px;
        &__header{
          margin-bottom: 20px;
          .label{
            display: flex;
            align-items: center;
          }
          display: flex;
          justify-content: space-between;

        }
        .input-range__label--value{
          color: #3b4954;
        }
        &__slider{

          background: $main-action-color;
          border: 1px solid  $main-action-color;
        }
        &__track{
          background: #b9b9b9;
          &--active{
            background: $main-action-color;
          }
        }

      }
    }
    .choosed-values{

      display: flex;
      align-items: center;
      border: 1px solid #b9b9b9;
      cursor: pointer;

      border-radius: 5px;
      padding: 10px 30px  10px 15px;
      background: white;
      position: relative;

    }
    .label{
      display: block;
      margin-bottom: 10px;
    }
    .dropdown{
      &.dropdown_active{
        .choosed-values{
          background: #F0F0F0 ;
        }
        .dropdown-select{
          display: block;
        }
      }
      position: relative;
      .input-group{
        flex-wrap: wrap;
        display: flex;
        align-items: center;
        border: 1px solid #b9b9b9;
        cursor: pointer;
        border-radius: 5px;
        padding: 3px 3px 3px 8px ;
        background: white;
        position: relative;
        &__input{
          width: 50px;
          padding: 7px 5px;
          flex-grow: 1;
        }

        &__added-values{
          display: flex;
          flex-wrap: wrap;
        }
        &__added-value {
          margin-right: 3px;
          cursor: default;
          font-size: 16px;
          background: #F0F0F0 ;
          border-radius: 5px;
          display: flex;
          align-items: center;
          border: 1px solid #c6c6c6;
          padding: 3px 5px 3px 5px;
          &:hover{
            background: #dedede;
          }
          span{
            &:first-child{
              margin-right: 4px;
            }
          }
          .close{
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 17px;
            width: 17px;
            &__icon{
              fill: #50646f;
              width: 8px;
              height: 8px;
            }
          }
        }
      }
      &__value{

        &:after{
          position: absolute;
          content: '';
          top: 55%;
          transform: translateY(-50%);
          right: 15px;
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 5px 5px 0 5px;
          border-color: #3b4954 transparent transparent transparent;
        }
        &:hover{
          background: #F0F0F0 ;
        }
      }
      &-select{
        display: none;
        z-index: 15;
        position: absolute;
        left: 0;
        right: 0;
        border-width: 0px 1px 1px 1px;
        border-style: solid;
        border-color: #b9b9b9;
        background: white;
        //display: none;
        height: 153px;
        overflow-y: auto;
        &__option{
        &.dropdown-select__option_active{
          background: #F0F0F0;
        }
        cursor: pointer;
        padding: 10px 30px;
        &:hover{
          background: #F0F0F0 ;
        }
      }
    }
  }
}
}

.year-settings{
  font-size: 14px;
  display: flex;
  & > * {
  	&:not(:last-child){
  		margin-right: 15px ;
  	}
  }
}
.year-setting{
  padding: 5px;
  border-radius: 5px;
  border: 1px solid transparent;
  user-select: none;
  display: flex;
  color: #5c6c79;
  &.year-setting_active{
    border-color: $main-action-color;
    color: #3b4954;
    input{
      background-color: $main-action-color;
    }
  }
  & > * {
  	&:not(:last-child){
  		padding-right: 5px;
  	}
  }
  &__label{
    cursor: pointer;
  }
  &__radio{
   input{
     display: block;
     position: relative;
     height: 15px;
     width: 15px;
     background-color: #eee;
     border-radius: 50%;
     cursor: pointer;
     &:after {
       position: absolute;
       top: 50%;
       left: 50%;
       transform: translateY(-50%) translateX(-50%);
       display: block;
       content: '';
       width: 8px;
       height: 8px;
       border-radius: 50%;
       background: white;

     }
   }
    display: flex;
    justify-content: center;
    align-items: center;

  }
}
