
.movie-card__actions{
  z-index: 1;
  transition: .5s;
  padding: 10px;
  right: -50px;
  position: absolute;
  display: flex;
  flex-direction: column;
  & > * {
    background: rgba(0, 0, 0, .6);
    padding: 3px;
    border-radius: 6px;
    cursor: pointer;
    margin: 5px 0;
    &:first-child {margin-top: 0 ;}
    &:last-child {margin-bottom: 0 ;}
  }
}
.add-to-list{
  &_active{
    .action-icon{
      animation-name: action-animate;
      animation-duration: 0.2s;
      animation-timing-function: ease-out;
      animation-iteration-count: 1;
    }
    .add-to-favorities__icon{
       fill: #D75A4A !important;
     }
    .add-to-wishlist__icon{
      fill: #EBB200!important;
    }
    .add-to-watched__icon{
      fill: #00CA33 !important;
    }
    .play-trailer__icon{
      fill: #00A5F6!important;
    }
  }
}

@keyframes action-animate {
  0% {
    transform: scale(1.0);
  }
  50%{
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}



.action-icon{

  fill: rgba(255,255,255, .7);
  width: 30px;
  height: 30px;

  &:hover{
    &.add-to-favorities__icon{
      fill: rgba(215,90,74, .8);
    }
    &.add-to-wishlist__icon{
      fill: rgba(235,178,0, .8);
    }
    &.add-to-watched__icon{
      fill: rgba(0,202,51,.8);
    }
    &.play-trailer__icon{
      fill: rgb(0,165,246);
    }
  }
}