.controls{
  cursor: pointer;
  padding: 4px;
  border-radius: 20px;
  background: white ;
  position: absolute;
  right: 0;
  top: -10px;
  display: flex;
  & > * {

    & > * {
      margin-bottom: 3px;
    }

    &:not(:last-child){
      margin-right: 12px ;
    }
  }
  .control{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    border-radius: 40px;
    background: #f1f3f6;
    border: 1px solid transparent;
    &:hover{
      border: 1px solid $main-action-color;

      .control__icon{
        fill: $main-action-color
      }
    }

    &__icon{
      width: 15px;
      height: 15px;
      fill: rgba(28, 6, 6, 0.5);
      &.left{
        transform: rotate(180deg);
      }
    }
    &.control__blocked{
      .control__icon {fill: rgba(101, 104, 109, 0.5);}
      &:hover{
        border: 1px solid transparent;

        .control__icon{
          fill: rgba(101, 104, 109, 0.5);
        }
      }
    }

  }
}

