.movie {
  &-page {
    background: #F0F0F0;
    color: #5c6c79;
    padding: 30px 40px;
    font-size: 20px;
  }
  &-inner {
    display: flex;

    &__col {
      &-1 {
        width: 25%;
        margin-right: 30px;
      }
      &-2 {
        width: 70%;
      }
    }

    .info-block{
      display: flex;
      margin-bottom: 20px;
      & > * {
        display: flex;
      }
      &__item{
        border-radius: 4px;
        padding: 2px 10px;
        color: #fff;
        text-transform: uppercase;
      }
      .basic-info{
        margin-right: 40px;
        & > * {
        	&:not(:last-child){
        		margin-right: 10px;
        	}
        }
      }
      .genres-info{
        & > * {
        	&:not(:last-child){
        		margin-right: 10px ;
        	}
        }
      }

      .genres-info__item{
        background: #7e8a97;
      }
      .year{
        background: $main-action-color;
      }
      .country{
        background: #7d7cce;
      }
      .lang{
        background: #94b5ce;
      }


    }
    .title-block{
      margin-bottom: 15px;
      .title{
        text-transform: uppercase;
      }
      .tagline{
        text-align: right;
        font-style: italic;
      }
    }
    .info-money{
      display: flex;
      & > * {
        width: 50%;
      }

    }

  }
  &-thumbnail{
    margin-bottom: 20px;
    height: 390px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &-actions{
    display: flex;
    justify-content: space-between;
    & > * {
      cursor: pointer;
      padding: 5px;
      background: none;
      border-radius: 7px;
    }
    .action-icon{
      fill:  #5c6c79 ;
      width: 35px;
      height: 35px;
    }
  }
}
.overview{
  line-height: 1.4;
  margin-bottom: 30px;
}

.movie-details{
  margin-bottom: 40px;
  & > * {
    span{
      text-decoration: underline;
      margin-right: 10px;
    }
    margin: 15px 0;
    &:first-child {margin-top: 0 ;}
    &:last-child {margin-bottom: 0 ;}
  }
}

.cast{
  &-title{
    border-radius: 5px;
    display: inline-block;
    background: white;
    padding: 5px 10px;
    font-size: 22px;
    margin-bottom: 10px;
  }
}

.rating{
  &-block{
    margin-bottom: 15px;
    display: flex;

    .rating{
      padding: 5px;
      border-radius: 5px;

    }
    & > * {
    	&:not(:last-child){
        margin-right: 15px;
    	}
    }
  }

  &__by{

    img{
      width: 65px;
    }
    .tmdb-logo{
      border-radius: 4px;
      width: 40px;
    }


  }
  display: flex;
  &__count{
    width: 45px;
    height: 45px;
    border-radius: 5px;
    margin-right: 10px;
    color: #fff;
    font-weight: 600;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    font-size: 25px;
    background: #00CA33;
  }
  &__info{
    display: flex;
    justify-content: center;


  }

}

.imdb-logo{
  background: orange;
  fill: black;
  width: 40px;
  height: 40px;
}

