.banner{
  width: 100%;
  display: inline-block;
  background-repeat: no-repeat;
  background-position: 50% 10%;
  background-size: cover;
  height: 480px;
  position: relative;
  &_fixed{
    background-attachment: fixed;
  }
  &__title{
    box-shadow: 0 0 40px 54px rgba(0,0,0,0.6);
    white-space:nowrap;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0,0,0,.6);
    font-size: 65px;
    position: absolute;
    bottom: 20px;
    text-align: center;
    color: white;
    margin-bottom: 20px;
    padding: 15px 20px;
    border-radius: 20px;
  }
}