.genres-page{
  padding: 105px 40px 30px 40px;
  color: #3b4954;
  background: #F0F0F0;
  &__categories{
    & > * {
    	&:not(:last-child){
    		margin-right: 30px ;
    	}
    }
  }
}
.genres{
  display: flex;
  flex-wrap: wrap;
  & > * {
    overflow: hidden;
    border-radius: 15px;
    height: 173px;
    width: 24%;
    margin: 0 14px 14px 0
  }
  .genre__title{
    font-size: 26px;
  }
}
.genre{
  &:nth-child(4n){
    margin-right: 0;
  }
}
