.contollers{
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: space-between;
  font-family: $main-font;
  top: -45px;

}


.switch-video{
  letter-spacing: 2.5px;
  font-size: 20px;
  text-transform: uppercase;
  display: flex;
  & > * {
  	&:not(:last-child){
  		margin-right: 15px ;
  	}
  }
  &__item{
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all linear .1s;
    cursor: pointer;
    padding: 5px 10px;
    border-radius: 5px;
    color: rgba(255, 255, 255, 0.5);
    border: 1px solid rgba(255, 255, 255, 0.5);
    &:hover{
      border: 1px solid white ;
      color: white;
      background: rgba(0, 0, 0, 0.3);
    }
    &.switch-video__item_active{
      cursor: default;
      color: white;
      border: 1px solid white ;
      background: rgba(0, 0, 0, 0.5);
      &:hover{
        border: 1px solid white ;
        color: white;
      }
    }
  }
}
.close-modal{
  cursor: pointer;
  margin-right: 10px;
  margin-top: 13px;
  &:hover{
    .close-modal__icon{
      fill: white;
    }
  }
  &__icon{
    width: 15px;
    height: 15px;
    fill: rgba(255, 255, 255, 0.5);
  }
}