.recommendations{
  &-page{
    padding: 105px 40px 30px 40px;
    color: #3b4954;
    background: #F0F0F0;
  }

}

.preferences-group{
  margin-bottom: 40px;
  position: relative;
  flex-wrap: wrap;
  display: flex;
  background: white;
  border-radius: 10px;
  align-items: center;
  border: 1px solid #b9b9b9;
  padding: 6px 6px  6px 14px;
  .preferences{
    display: flex;
  }
  &.preferences-group_active{
    .preference-suggestions{

      display: block;
    }
    border-radius: 10px 10px 0 0;
  }
  .preference{

    display: flex;
    align-items: center;

    padding: 5px 10px;
    justify-content: center;
    .text{
      margin-right: 6px;
    }
    .close{
      cursor: pointer;
      padding: 0 4px;
    }
    .close__icon{
      fill: #3b4954;
      width: 9px;
      height: 9px;
    }
  }

  .preferences-input{
    flex-grow: 1;
    padding: 5px 0;
  }
  .preference-suggestions{
    z-index: 4;
    display: none;
    left: -1px;
    right: -1px;
    transform: translateY(100%);
    bottom: -1px;
    background: white;
    position: absolute;
  }
  .preference-suggestion{
    display: flex;
    align-items: center;
    border-width: 0 1px 1px 1px;
    border-style: solid ;
    border-color: #b9b9b9;
    cursor: pointer;
    padding: 8px 30px;
    &:hover{
      background: #F0F0F0;
    }
    .title{
      margin-right: auto;
    }
    .info{
      align-items: center;
      display: flex;
      & > * {
      	&:not(:last-child){
      		margin-right: 15px ;
      	}
      }
      .year{
        padding: 1px 3px;
        border-radius: 3px;
        border: 1px solid #3b4954;
      }
      .add-to-favorities__icon{
        width: 22px;
        height: 22px;
        fill: #5c6c79;
        &:hover{
          fill: #D75A4A !important;
        }

      }
      .add-to-wishlist__icon{
        width: 22px;
        height: 22px;
        fill: #5c6c79;
        &:hover{
          fill: #EBB200;
        }

      }
      .add-to-watched__icon{
        width: 22px;
        height: 22px;
        fill: #5c6c79;
        &:hover{
          fill: #00CA33;
        }
      }
      .play-trailer{
        &__icon{
          width: 22px;
          height: 22px;
          fill: #5c6c79;
          &:hover{
            fill: #00A5F6;
          }

        }
      }

    }

  }
}