.genre{
  display: block;
  cursor: pointer;
  text-transform: uppercase;
  height: 100px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  &-arrow{
    cursor: pointer;
    top: -31px;
    position: absolute;
    :hover{
      .genre-arrow__icon{
        fill: rgba(40, 40, 40, 0.8);
      }
    }

    &__icon{
      fill: rgba(101, 104, 109, 0.5);
      width: 15px;
      height: 15px;
    }
  }
  &__title{
    text-align: center;
    color: white;
    font-size: 20px;
    transition: all linear .3s;
  }
  &-overlay{
    transition: all linear .3s;
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover{
      background: rgba(0, 0, 0, 0.65);
      .genre__title{
        transform: scale(1.2);
      }
    }

  }

  &-action{
    background-image: url("../../static/images/genres/action.jpg");
  }
