.banner-slider{
  padding-top: 75px;
  color: black;
  position: relative;
  &__item{
    display: block;

  }


  .slick-dots{
    display: flex !important;
    background: rgba(0,0,0,.5);
    border-radius: 15px;
    width: auto;
    height: 30px;
    right: 50%;
    transform: translateX(50%);
    top: 90px;
    position: absolute;
    z-index: 1;
    align-items: center;
    justify-content: space-around;
    li{
      justify-content: center;
      display: flex;
      align-items: center;

      button{
        justify-content: center;
        display: flex;
        align-items: center;
        height: 12px;
        width: 12px;
        border-radius: 50%;
        background: rgba(255,255,255, 0.5);
      }
      button:before{
        content: none;
      }
      &.slick-active{
        button{
          background: $main-action-color;
        }
      }
    }
  }
}

