.movie {
   &-card {
     display: block;
     overflow: hidden;
     position: relative;
     width: 19%;
     margin-bottom: 15px;
     margin-right: 14px;
     &:nth-child(5n){
       margin-right: 0;
     }
     &__img{
       display: block;
       height: 100%;
     }
     img{
       object-fit: cover;
       height: 100%;
       transition: 0.4s;
     }
     &:hover{
       img{
         filter: brightness(130%);
       }
       .movie-card__actions{
         right: 0;
       }
       .movie-card__info{
         bottom: 0;
       }
     }
     &__img {
       img {
         width: 100%;
       }
     }
     &__info{
       transition: .5s;
       padding: 15px 20px;
       color: white;
       left: 0;
       right: 0;
       position: absolute;
       bottom: -95px;
       background: rgba(0, 0, 0, .6);
     }
     &__title{
       margin-bottom: 10px;
       font-size: 18px;
     }
     &__actions{
       z-index: 1;
       transition: .5s;
       padding: 10px;
       right: -50px;
       position: absolute;
       display: flex;
       flex-direction: column;
       & > * {
         background: rgba(0, 0, 0, .6);
         padding: 3px;
         border-radius: 6px;
         cursor: pointer;
         margin: 5px 0;
         &:first-child {margin-top: 0 ;}
         &:last-child {margin-bottom: 0 ;}
       }

     }
     &__stat{
       display: flex;
       .year{
         padding: 0 3px;
         border-radius: 4px;
         border: 1px solid white;
         margin-right: auto;
         margin-top: 4px;
         display: flex;
         align-items: center;
         justify-content: center;
       }
       .stars{
         margin-bottom: 3px;
         font-size: 20px;
       }
     }
   }
 }



.marks{
  z-index: 1;
  position: absolute;
  top: 15px;
  display: flex;
  flex-direction: column;
  color: white;
  .new{
    padding: 0 5px;
    background: #ffae00;
    margin-bottom: 5px;

  }
  .adult{
    padding: 0 8px;
    text-align: center;
    background: red;

  }
}


.movie-card_lg{
  margin-right: 0;
  width: 32%;
  margin-bottom: 0;
  .movie-card__img{
    height: 320px;
  }
  .movie-card__title{
    font-size: 22px;
  }
  .year{
    font-size: 20px;
    padding: 0 10px;
  }
  .stars{
    span{
      font-size: 30px !important;
    }
  }
}
.movie-card_sm{
  margin-right: 0;
  margin-bottom: 0;
  width: 17%;
}
