.block-movies{
  color: #3b4954;
  padding: 30px 40px;
  &__header{
    position: relative;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    .title{
      margin-right: 40px;
    }
    .categories{

      display: flex;
    }
    .category{
      cursor: pointer;
      padding: 5px 10px;
      color: #5c6c79;
      border-radius: 5px;
      &.category_active{
        background: white;
        color: black;
      }
      &:hover{
        background: white;
        color: black;
      }
    }
    .block-movies__categories{
      & > * {
        &:not(:last-child){
          margin-right: 25px;
        }
      }
    }
    .categories{
      font-size: 20px;

    }

    // pagination

    .controls{
      cursor: pointer;
      padding: 4px;
      border-radius: 20px;
      background: white ;
      position: absolute;
      right: 0;
      top: -10px;
      display: flex;
      & > * {

        & > * {
          margin-bottom: 3px;
        }

      	&:not(:last-child){
      		margin-right: 12px ;
      	}
      }
      .control{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        border-radius: 40px;
        background: #f1f3f6;
        border: 1px solid transparent;
        &:hover{
          border: 1px solid $main-action-color;

          .control__icon{
            fill: $main-action-color
          }
        }

        &__icon{
          width: 15px;
          height: 15px;
          fill: rgba(28, 6, 6, 0.5);
          &.left{
            transform: rotate(180deg);
          }
        }
        &.control__blocked{
          .control__icon {fill: rgba(101, 104, 109, 0.5);}
          &:hover{
            border: 1px solid transparent;

            .control__icon{
              fill: rgba(101, 104, 109, 0.5);
            }
          }
        }

      }
    }

  }
}
.movies-container{
  flex-wrap: wrap;
  display: flex;
  flex: 1 ;

}