@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";


.header{
  z-index: 9;
  background-color: black;
  position: fixed;
  right: 0;
  left: 0;
}

.navbar{
  align-items: center;
  display: flex;
  &-menu{
    position: relative;
    &.navbar-menu__open{
      .navbar-menu__sandwich{
        background: $main-action-color;
        border-radius: 5px 5px 0 0 ;
      }
      .dropdown-menu{
        display: flex;


      }
    }
    &__sandwich{
      cursor: pointer;
      margin-right: 10px;
      padding: 15px;
      border-radius: 3px;
      &:hover{
        transition: .3s;
        background: $main-action-color;
      }
      .sandwich__icon{
        fill: white;
        width: 30px;
        height: 30px;
      }
  }
    .dropdown-menu{
      display: none;
      background: $main-action-color;
      font-size: 20px;
      width: 200px;
      position: absolute;
      color: red;
      flex-direction: column;
      &__item{
        color: white;
        padding: 10px 15px ;
        &:hover{
          color: $main-action-color;
          background: white;
        }
      }
    }

  }
}
.logo{
  margin: 15px 20px 15px 0;
  &__icon{
    border-radius: 4px;
    height: 45px;
  }
}
.nav-list{
  font-size: 20px;
  display: flex;
  text-transform: uppercase;
  align-items: center;
  margin-right: auto;
  & > * {
    &:not(:last-child){
      margin-right: 15px ;
    }
  }
  &__item{
    &:hover{
      a{
        transition: .3s;
        color: $main-action-color;
      }
      a:after{
        border-color: $main-action-color transparent transparent transparent;
      }
    }
  }
}


.search{
  &-form{
    max-width: 280px;
    margin-right: 10px;
    position: relative;
    &.search-form_active{
      .search-group{
        border-radius: 15px 15px 0 0 ;
      }
      .search-suggestions{
        display: flex;
      }
    }
  }
  &-group{
    display: flex;
    align-items: center;
    border-radius: 15px;
    background: white;
    padding: 10px 5px;
    position: relative;
  }
  &-input{
    padding: 0 25px 0 10px;
    font-size: 20px;
  }
  &-btn{
    &__icon{
      top: 50%;
      transform: translateY(-50%);
      position: absolute;
      right: 6px;
      width: 25px;
      height: 25px;
    }
  }
}
.search-suggestions{
  width: 100%;
  margin-top: 2px;
  position: absolute;
  display: none;
  flex-direction: column;
  box-shadow: rgba(0,0,0,0.8) 0 4px 14px 0;
}
.search-suggestion{
  position: relative;
  border-bottom: 1px solid #efefef;
  font-size: 16px;
  color: black;

  display: flex;
  padding: 3px 6px;
  background: white;
  &__img{
    margin-right: 15px;
    img {
      width: 40px;
    }
  }
  &__label{
    padding: 5px;
    width: 100%;
    .title{
      font-size: 16px;
      margin-bottom: 8px;
    }
    .details{
      font-size: 14px;
      display: flex;
      & > * {
      	&:not(:last-child){
      		margin-right: 13px ;
      	}
      }
      .year{
        padding: 1px 3px;
        border-radius: 3px;
        border: 1px solid black;
      }
      .media-type{
        padding: 1px 3px;
        border-radius: 3px;
        border: 1px solid black;
      }
      .stars{
        margin-left: auto;
        margin-right: 15px;
      }
    }
  }
  &:hover{
    background: $main-action-color;
    cursor: pointer;
    color: white;
    .year{
      border-color: white;
    }
    .media-type{
      border-color: white;
    }
  }
}

.user-lists{
  &__link{
    margin-right: auto;
  }
  position: relative;
  cursor: pointer;
  &__icon{
    width: 20px;
    height: 20px;
    fill: white;
    margin-right: 5px;
    transition: .3s;
  }
  &__btn{
    border-radius: 5px;
    padding: 10px 15px;
    font-size: 20px;
    text-transform: uppercase;
    transition: .3s;
    &:after{
      position: absolute;
      right: 0;
      transform: translateY(-50%);
      top: 50%;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 5px 5px 0 5px;
      border-color: #ffffff transparent transparent transparent;
    }

    span{
      position: relative;
      margin-right: 15px;
      &:after{
        content: '';
        top: 55%;
        transform: translateY(-50%);
        position: absolute;
        right: -17px;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 6px 6px 0 6px;
        border-color: #ffffff transparent transparent transparent;
      }
    }
  }
  &:hover{
   .user-lists__btn{
     border-radius: 5px 5px 0 0 ;
     background: white;
     color: $main-action-color;
   }
    .user-lists__icon{
      fill: $main-action-color;
    }
    span:after{
      border-color: $main-action-color transparent transparent transparent;
    }
    .dropdown-menu{
      display: flex;
    }
  }
  .dropdown-menu{
    position: absolute;
    width: 100%;
    background: white;
    display: none;
    flex-direction: column;
    flex: 1;
    &__item{
      padding: 10px 10px;
      align-items: center;
      font-size: 18px ;
      display: flex;
      color: black;
      &:hover{
        background: $main-action-color;
        color: white;
        .dropdown-menu__icon{
          fill: white;
        }
      }
    }
    &__icon{
      margin-right: 10px;
      width: 15px;
      height: 15px;
    }
  }

}
.recommendations{
  position: relative;
  &:hover{
    .dropdown-menu{
      display: flex;

    }
  }
  .dropdown-menu{
    display: none;
    margin-left: 15px;
    text-transform: none;
    color: black;
    width: 100%;
    background: white;
    flex-direction: column;
    position: absolute;
    &__item{
      padding: 5px 15px;
      .tet{
        margin-right: auto;
      }
      &:hover{
        color: white;
        background: $main-action-color;
      }
    }
  }
}




